<template>
  <div class="ratings" ref="ratings">
    <div class="ratings-content">
      <!--  -->
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <div class="title"></div>
          <div class="rank">{{seller.rankRate}}</div>
        </div>
        <div class="overview-right">
          <div class="score-wrapper">
            <span class="title"></span>
            <Star :size="36" :score="seller.serviceScore"></Star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrapper">
            <span class="title"></span>
            <Star :size="36" :score="seller.foodScore"></Star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-wrapper">
            <span class="title"></span>
            <span class="delivery">{{seller.deliveryTime}}</span>
          </div>
        </div>
      </div>
      <!--  -->
      <Split></Split>
      <!--  -->
      <Ratingselect
        :ratings="ratings"
        :selectType="selectType"
        :onlyContent="onlyContent"
        @ratingtypeSelect="ratingtypeSelect"  @contentToggle="contentToggle" 
      ></Ratingselect>
      <div class="ratings-wrapper">
        <ul>
          <li v-show="needShow(rating.rateType,rating.text)" v-for="(rating,index) in ratings" :key="index" class="rating-item">
            <div class="avatar">
              <img :src="rating.avatar" width="28px" height="28" />
            </div>
            <div class="content">
              <h1 class="name">{{rating.username}}</h1>
              <div class="star-wrapper">
                <Star :size="24" :score="rating.score"></Star>
                <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}</span>
              </div>
              <p class="text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend.length">
                <span class="icon-thumb_up"></span>
                <span class="item" v-for="(item,i) in rating.recommend " :key="i">{{item}}</span>
              </div>
              <div class="time">{{rating.rateTime}}</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll";
import Star from "../star/Star";
import Ratingselect from "./../ratingselect/Ratingselect";
import Split from "./../split/Split";
import MyData from "./../../mock/data.js";

const ALL = 2;

export default {
  data() {
    return {
      ratings: [],
      showFlag: false,
      selectType: ALL,
      onlyContent: true
    };
  },
  components: {
    Star,
    Split,
    Ratingselect
  },
  props: {
    seller: {
      type: Object
    }
  },
  methods:{
           ratingtypeSelect(type){
           console.log("ratingtypeSelect",type);
           this.selectType=type;
           this.$nextTick(()=>{
                this.bscroll.refresh();
           })
           
       },
       contentToggle(onlyContent){
           console.log("contentToggle",onlyContent);
           this.onlyContent=onlyContent;
           this.$nextTick(()=>{
                this.bscroll.refresh();
           })
       },
           needShow(type,text){
           if(this.onlyContent && !text){
               return false;
           }
           if(this.selectType === ALL){
               return true;
           }else {
               return type === this.selectType;
           }
       },
  },
  created() {
    console.log("goods", MyData);
    this.ratings = MyData.ratings;
    this.$nextTick(() => {
      if (!this.bscroll) {
        this.bscroll = new BScroll(this.$refs.ratings, { click: true });
      }
    });
  }
};
</script>

<style lang="stylus" scoped>
@import '../../common/stylus/mixin'
.ratings
  position absolute
  top 174px
  bottom 0
  left 0
  width 100%
  overflow hidden
  .overview
    display flex
    padding 18px 0
    .overview-left
      flex 0 0 137px
      padding 6px 0
      width 137px
      border-right 1px solid rgba(7, 17, 27, 0.2)
      text-align center
      @media only screen and (max-width: 320px)
        flex 0 0 120px
        width 120px
      .score
        line-height 28px
        font-size 24px
        color rgb(255, 153, 0)
      .title
        margin-bottom 8px
        line-height 12px
        font-size 12px
        color rgb(7, 17, 27)
      .rank
        line-height 10px
        font-size 10px
        color rgb(147, 153, 169)
    .overview-right
      flex 1
      padding 6px 0 6px 24px
      @media only screen and (max-width: 320px)
        padding-left 6px
      .score-wrapper
        margin-bottom 8px
        font-size 0
        .title
          display inline-block
          line-height 18px
          vertical-align top
          font-size 12px
          color rgb(7, 17, 27)
        .star
          display inline-block
          margin 0 12px
          vertical-align top
        .score
          display inline-block
          line-height 18px
          vertical-align top
          font-size 12px
          color rgb(255, 153, 0)
      .delivery-wrapper
        font-size 0
        .title
          line-height 18px
          font-size 12px
          color rgb(7, 17, 27)
        .delivery
          margin-left 12px
          font-size 12px
          color rgb(147, 153, 169)
  .ratings-wrapper
    padding 0 18px
    .rating-item
      display flex
      padding 18px 0
      border-1px(rgba(7, 17, 27, 0.1))
      .avatar
        flex 0 0 28px
        width 28px
        margin-right 12px
        img
          border-radius 50%
      .content
        position relative
        flex 1
        .name
          margin-bottom 4px
          line-height 12px
          font-size 10px
          color rgb(7, 17, 27)
        .star-wrapper
          margin-bottom 6px
          font-size 0
          .star
            display inline-block
            margin-right 6px
            vertical-align top
          .delivery
            display inline-block
            margin-right 6px
            vertical-align top
            line-height 12px
            font-size 10px
            font-weight 200
            color rgb(147, 153, 159)
        .text
          margin-bottom 8px
          line-height 18px
          color rgb(7, 17, 27)
          font-size 10px
        .recommend
          line-height 16px
          font-size 0
          .icon-thumb_up,.item
            display inline-block
            margin 0 8px 4px 0
            font-size 9px
          .icon-thumb_up
            color rgb(0,160,220)
          .item
            padding 0 6px
            border 1px solid rgba(7,17,27,0.1)
            border-radius 1px
            color rgb(147,153,159)
            background #ffffff
        .time
          position absolute
          top 0
          right 0




</style>
