<template>
  <!-- better-scroll    -->
  <div class="seller" ref="seller">
    <div class="seller-content">
      <div class="overview">
        <h1 class="title">{{seller.name}}</h1>
        <div class="desc broder-1px">
          <Star :size="36" :score="seller.score"></Star>
          <span class="text">({{seller.ratingCount}})</span>
          <span class="text">{{seller.sellCount}}</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2></h2>
            <div class="content">
              <span class="stress">{{seller.minPrice}}</span>
            </div>
          </li>
          <li class="block">
            <h2></h2>
            <div class="content">
              <span class="stress">{{seller.deliveryPrice}}</span>
            </div>
          </li>
          <li class="block">
            <h2></h2>
            <div class="content">
              <span class="stress">{{seller.deliveryTime}}</span>
            </div>
          </li>
        </ul>
        <div class="favorite" @click="toggleFavorite">
          <span class="icon-favorite" :class="{isActive:favorite}"></span>
          <span class="text">{{favoriteText}}</span>
        </div>
      </div>
      <Split></Split>
      <div class="bulletin">
        <h1 class="title"></h1>
        <div class="content-wrapper">
          <div class="content">{{seller.bulletin}}</div>
        </div>
        <!-- classMap  _2  -->
        <ul v-if="seller.supports" class="supports">
          <li class="support-item border-1px" v-for="(item,index) in seller.supports" :key="index">
            <span class="icon" :class="classMap[seller.supports[index].type]"></span>
            <span class="text">{{seller.supports[index].description}}</span>
          </li>
        </ul>
      </div>
      <Split></Split>
      <div class="pics">
        <h1 class="title"></h1>
        <div class="pic-wrapper" ref="picWrapper">
          <ul class="pic-list" ref="picList">
            <li class="pic-item" v-for="(pic,i) in seller.pics" :key="i">
              <img :src="pic" width="120" height="90" />
            </li>
          </ul>
        </div>
      </div>
      <Split></Split>
      <div class="info">
        <h1 class="title border-1px"></h1>
        <ul>
          <li class="info-item" v-for="(info,i) in seller.infos" :key='i'>{{info}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll";
import Star from "../star/Star";
import Split from "../split/Split";
import MyData from "./../../mock/data.js";
import {saveToLocal,loadFromLocal} from "./../../common/js/store"

export default {
  data() {
    return {
      seller: {},
      favorite:(()=>{
        return loadFromLocal("123345456","favorite",false)
      })()
    };
  },
  props: {
    // seller: {
    //   type: Object
    // }
  },
  computed:{
    favoriteText(){
      return this.favorite ? "" : "";
    }
  },
  created() {
    this.classMap = ["decrease", "discount", "special", "invoice", "guarantee"];
    this.seller = MyData.seller;
  },
  ready() {
    console.log(this.seller);
  },
  watch: {
    seller() {
      console.log("1");
      this._initScroll();
      this._initPicScroll();
    }
  },
  methods: {
    toggleFavorite(event){
      if(!event._constructed){
        return;
      }
      this.favorite = !this.favorite;
      saveToLocal(this.seller.id,'favorite',this.favorite);

    },
    _initScroll() {
      if (!this.scroll) {
        this.bscroll = new BScroll(this.$refs.seller, {
          click: true
        });
      }
    },
    _initPicScroll() {
      if (this.seller.pics) {
        let picWidth = 120;
        let margin = 6;
        let width = (picWidth + margin) * this.seller.pics.length - margin;
        this.$refs.picList.style.width = width + "px";
        this.$nextTick(() => {
          if(!this.picBscroll){
            this.picBscroll = new BScroll(this.$refs.picWrapper, { click: true ,scrollX: true,eventPassThrough:'vertical'});
          }else{
            this.picBscroll.refresh();
          }
        });
      }
    }
  },
  components: {
    Star,
    Split
  }
};
</script>

<style lang="stylus" scoped>
@import '../../common/stylus/mixin'
.seller
  position absolute
  top 174px
  bottom 0
  left 0
  width 100%
  overflow hidden
  .overview
    position relative
    padding 18px
    .title
      margin-bottom 8px
      line-height 14px
      color rgb(7, 17, 27)
      font-size 14px
    .desc
      padding-bottom 18px
      line-height 18px
      border-1px(rgba(7, 17, 27, 0.1))
      font-size 0
      .star
        display inline-block
        margin-right 8px
        vertical-align top
      .text
        margin-right 12px
        display inline-block
        vertical-align top
        font-size 10px
        color rgb(77, 85, 93)
    .remark
      display flex
      padding-top 18px
      .block
        flex 1
        text-align center
        border-right 1px solid rgba(7, 17, 27, 0.2)
        &:last-child
          border none
        h2
          margin-bottom 4px
          line-height 10px
          font-size 10px
          color rgb(147, 153, 159)
        .content
          line-height 24px
          font-size 10px
          color rgb(7, 17, 27)
          .stress
            font-size 24px
    .favorite
      position absolute
      width 50px
      right 11px
      top 18px
      text-align center
      .icon-favorite
        display block
        margin-bottom 4px
        line-height 24px
        font-size 24px
        color #d4d6d9
        &.isActive
          color rgb(240,20,20)
      .text
        line-height 10px
        color rgb(77,85,93)
        font-size 10px
  .bulletin
    padding 18px 18px 0 18px
    .title
      margin-bottom 8px
      line-height 14px
      color rgb(7, 17, 27)
      font-size 14px
    .content-wrapper
      padding 0px 12px 16px 12px
      border-1px(rgba(7, 17, 27, 0.1))
      .content
        line-height 24px
        font-size 12px
        color rgb(240, 20, 20)
    .supports
      .support-item
        padding 16px 12px
        border-1px(rgba(7, 17, 27, 0.1))
        font-size 0
      .icon
        display inline-block
        vertical-align top
        width 12px
        height 12px
        margin-right 4px
        background-size 12px 12px
        background-repeat no-repeat
        &.decrease
          bg-image('decrease_4')
        &.discount
          bg-image('discount_4')
        &.guarantee
          bg-image('guarantee_4')
        &.invoice
          bg-image('invoice_4')
        &.special
          bg-image('special_4')
      .text
        line-height 16px
        font-size 12px
  .pics
    padding 18px
    .title
      margin-bottom 12px
      line-height 14px
      color rgb(7, 17, 27)
      font-size 14px
    .pic-wrapper
      width 100%
      overflow hidden
      white-space nowrap
      .pic-list
        font-size 0
        .pic-item
          display inline-block
          margin-right 6px
          width 120px
          height 90px
          &:last-child
            margin-right 0
  .info
    padding 18px 18px 0 18px
    .title
      padding-bottom 12px
      line-height 14px
      border-1px(rgba(7, 17, 27,0.1))
      color rgb(7, 17, 27)
      font-size 14px
    .info-item
      padding 16px 12px
      color rgb(7, 17, 27)
      font-size 12px
      line-height 16px
      border-1px(rgba(7, 17, 27,0.1))
</style>
